@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,300&display=swap');

.MainDiv {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
}

.rightGrid {
  overflow: auto;
  margin-left: 50%;
  left: 50;
  position: relative;
}
.LeftGrid {
  position: fixed;
  top:0;
  width: 30%;
  
  height: 100%;
  float:left;
}

span {
  color: #313233;
}

li {
  margin-left: 2%;
  font-size: 15px;
  margin-top: 2%;
}

h3 {
  font-size: 15px;
  color: #969696;
  width: 85%;
}

h4, h2 {
  color: #313233;
  font-size: 20px;
  margin-bottom: 10px;
}

.LeftGrid {
  width: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: flex-start;
  border-right: 1px solid #696969;
 
}

.title {
  cursor: pointer;
}

.LeftGrid h1 {
  font-family: 'Roboto', sans-serif;
  font-size: 35px;
  color: #0a67ca;
}

.LeftGrid span {
  margin-top: 3%;
  color: #969696;
}

.LeftGrid small {
  margin-top: 4%;
  color: #969696;
  font-size: 14px;
}



.LeftGrid img {
  border-radius: 8px;
}

.rightGrid {
  display: flex;

  flex-direction:  column;
  width: 70%;
  margin-left: 32%;
 
}

.rightGrid span, small {
  width: 90%;
}

.rightGrid small {
  margin-top: 2%;
}

.Articles {
  display: flex;
  flex-direction: column;
  width: 90%;
}

.title{
  margin-top: 2%;
  text-decoration: none;
  color: #0a67ca;
 
}

a {
  text-decoration: none;
  color: #0a67ca;
}

.rowed {
  margin-bottom: 2%;
  margin-top: 2%;
  display: flex;
  border-bottom: 1px solid #696969;

}

.rowed small {
  color: #fff;
}

.rowed div {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 20%;
  height: 30px;
  background-color: #727272;
  border-radius: 4px;
  margin-right: 10px;
  margin-bottom: 5%;
 
  
}

.subject {
  color: #313233;
  font-size: 15px;
  margin-top: 2%;
}



* {
  font-family: 'Roboto', sans-serif;
}

.place {
  margin-top: 2%;
  font-size: 12px;
  color: #0a67ca;
}

.Info {
  margin-top: 2%;
  margin-bottom: 2%;
  display: flex;
  align-items: center;

}

.Info a {
  margin-left: 1%;
  text-decoration: none;
  color: #0a67ca;
 
}

.course {
  height: 80px;
  width: 80%;
  margin-top: 20px;
  background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 19%, rgba(0,212,255,1) 100%);
  border-radius: 10px;
  border: none;
  cursor: pointer;
  -webkit-box-shadow:  0 4px 2px -2px gray; 
  box-shadow: 0 4px 2px -2px gray;
  display: flex;
  justify-content: center;
  align-items: center;
}
.course:hover {
  transition: all 0.3s;
  background: linear-gradient(90deg, rgb(44, 39, 139) 0%, rgb(41, 41, 169) 19%, rgb(71, 132, 144) 100%);
}

.logo {
  height: 150px;
  width: 150px;
  border-radius: 50%;
}

.cta {
  margin-top: 10px;
}